import React, {Component} from "react"
// import {Link} from "gatsby"

const imgWrapper = {
    width: "100%"
}

const contentWrapper = {
    textAlign: "center",
    marginTop: "5%"
}

const date = {
    fontSize: "14pt",
    fontWeight: "400",
    fontStyle: "italic"
}

class Cover extends Component {
    constructor(props) {
        super(props)

        this.children = React.Children.toArray(props.children)
        this.width = props.width
        this.coverStyle = {
            position: "absolute",
            width: this.width,
            // boxShadow: "0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12)"
        }
        this.coverClass = props.className

        this.dest = props.external
    }

    render() {
        return (
            <a href={this.dest} style={this.coverStyle} className={this.coverClass}>
                <div style={imgWrapper}>{this.children[0]}</div>
                <div style={contentWrapper}>
                    <div>{this.children[1]}</div>
                    <div style={date}>{this.children[2]}</div>
                </div>
            </a>
        )
    }
}

export default Cover
